import axios from 'axios'
import con from './config'


const state = {
       links: [],
       sublinks: [],
       personnel: [],
}

const getters = {
       allLinks: state => state.links,
       allSubLinks: state => state.sublinks,
       allPersonnel: state => state.personnel,
}
const actions = {
       async getIISToken({ }, t) {

              await axios.get(`https://iis.emb.gov.ph/embis/pbsapi/?token=${t.token}&token_id=${t.id}`, {

              }).then(function (res) {
                     // check to swms user account and pull the responsibility links

                     sessionStorage.setItem('origin', 'iis');
                     sessionStorage.setItem('cregion', res.data['region']);
                     sessionStorage.setItem('region_id', res.data['region_id']);
                     //sessionStorage.setItem('name', res.data['first_name'] + " " + res.data['last_name']);

                     con.post("/auth/login", {
                            iis_employee_number: res.data['employee_id'],
                            origin: 0
                     }).then(function (log) {
                            console.log(log)
                            if (log.data.user['region'] == "18") {
                                   sessionStorage.setItem('user_type', 'central');
                            } else {
                                   sessionStorage.setItem('user_type', 'region');
                            }
                            sessionStorage.setItem('ascwsktken', log.data.access_token);
                            sessionStorage.setItem('access', log.data.user['user_access_level']);
                            sessionStorage.setItem('iis_employee_number', log.data.user['id']);
                            sessionStorage.setItem('name', log.data.user['name']);

                            window.location.href = "/"; // for development
                            // window.location.href = "http://swm.emb.gov.ph/"; // for production
                     }).catch(error => {
                            console.log(error.response.data.error)
                            //window.location.href = "https://iis.emb.gov.ph/embis/swmapiauth_loc?token=41306029d7d2064ce"
                            if (error.response.data.error == "Expired token") {
                                   sessionStorage.clear();
                                   window.location.href = "https://iis.emb.gov.ph/embis/";
                            }
                     })

              }).catch(error => {

              })

       },
       // http://localhost:8080/?token_id=4&token=2524&origin=crs#/
       async getCRSToken({ }, t) {
              await axios.get(`https://iis.emb.gov.ph/embis/api/Crs_Api/json_company_swm?api_key=x&company_id=${t.company_id}&user_id=${t.user_id}`, {

              }).then(function (res) {
                     sessionStorage.setItem('origin', 'crs');
                     sessionStorage.setItem('cregion', res.data['region_name']);
                     sessionStorage.setItem('region_id', res.data['rgnid']);
                     sessionStorage.setItem('province_id', res.data['province_id']);
                     sessionStorage.setItem('city_id', res.data['city_id']);
                     //sessionStorage.setItem('name', res.data['userinfo']['first_name'] + " " + res.data['userinfo']['last_name']);

                     sessionStorage.setItem('user_type', 'lgu');
                     con.post("/auth/login", {
                            iis_employee_number: res.data['userinfo']['client_id'],
                            origin: 1
                     }).then(function (log) {
                            sessionStorage.setItem('ascwsktken', log.data.access_token);
                            sessionStorage.setItem('access', log.data.user['user_access_level']);
                            sessionStorage.setItem('iis_employee_number', log.data.user['id']);
                            sessionStorage.setItem('name', log.data.user['name']);



                            window.location.href = "/"; // for development
                     })
                            .catch(error => {
                                   console.log(error.response.data.error)
                                   window.location.href = "https://client.emb.gov.ph/crs/"
                                   if (error.response.data.error == "Expired token") {
                                          sessionStorage.clear();
                                          window.location.href = "https://client.emb.gov.ph/crs/";
                                   }
                            })
              }).catch(error => {

              })



       },

       async getSidebar({ commit }, res) {
              //console.log(res.access_level);

              await con.get(`/GetResLink/${res.access_level}`).then(function (res) {
                     commit('allLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
                     // if (error.response.data.error == "Wrong number of segments") {
                     //        sessionStorage.clear();
                     //        window.location.href = "https://iis.emb.gov.ph/embis/";
                     // }

              })


       },

       async getSubSidebar({ commit }, res) {
              await con.get(`/GetSubResLink/${res.access_level}`).then(function (res) {
                     commit('allSubLinks', res.data['Res']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },

       async fetchPersonnel({ commit }, res) {
              await con.get(`/GetPersonnel/${res.reg_id}/${res.user_id}`).then(function (res) {
                     commit('allPersonnel', res.data['data']);
              }).catch(error => {
                     if (error.response.data.error == "Expired token") {
                            sessionStorage.clear();
                            window.location.href = "https://iis.emb.gov.ph/embis/";
                     }
              })


       },


};

const mutations = {
       allLinks: (state, User) => (state.links = User),
       allSubLinks: (state, User) => (state.sublinks = User),
       allPersonnel: (state, User) => (state.personnel = User),
}

export default {
       state,
       getters,
       actions,
       mutations
};